// Component transition animations
// Usage: Add [@animationName] to the component tag after importing it

import { animate, AnimationTriggerMetadata, state, style, transition, trigger } from '@angular/animations';

export const fadeAnimation: AnimationTriggerMetadata =
trigger('initFadeAnimation', [
  state('*',
    style({
      opacity: 1,
    }),
  ),
  transition(':enter', [
    style({
      opacity: 0,
    }),
    animate('0.3s ease-in'),
  ]),
  transition(':leave', [
    animate('0.5s ease-out', style({
      opacity: 0,
    })),
  ]),
]);
export const slideInLeftAnimation: AnimationTriggerMetadata =
trigger('initSlideInLeftAnimation', [
  state('*',
    style({
      opacity: 1,
      transform: 'translateX(0)',
    }),
  ),
  transition(':enter', [
    style({
      opacity: 0,
      transform: 'translateX(-100%)',
    }),
    animate('0.3s ease-in'),
  ]),
  transition(':leave', [
    animate('0.5s ease-out', style({
      opacity: 0,
      transform: 'translateX(100%)',
    })),
  ]),
]);
export const slideInDownAnimation: AnimationTriggerMetadata =
trigger('initSlideDownAnimation', [
  state('*',
    style({
      opacity: 1,
      transform: 'translateY(0)',
    }),
  ),
  transition(':enter', [
    style({
      opacity: 0,
      transform: 'translateY(-100%)',
    }),
    animate('0.3s ease-in'),
  ]),
  transition(':leave', [
    animate('0.5s ease-out', style({
      opacity: 0,
      transform: 'translateY(100%)',
    })),
  ]),
]);



